import React, { useEffect, useState } from "react";
import { TemplateHandler } from "easy-template-x";
import { Button } from "semantic-ui-react";
var _ = require("lodash");

const WordTemplateReplace = ({ filepath, filename, data, render }) => {
  const [newData, setNewData] = useState({});

  const convertData = (data) => {
    let returnData = { ...data };

    const allkeys = Object.keys(returnData);
    allkeys.forEach((key) => {
      if (returnData[key]) {
        if (_.isArray(returnData[key])) {
          returnData[key].forEach((subobject, subindex) => {
            const subkeys = Object.keys(subobject);
            subkeys.forEach((subkey) => {
              if (returnData[key][subindex][subkey].isImage) {
                getImage(
                  returnData[key][subindex][subkey].url,
                  returnData[key][subindex][subkey].width,
                  returnData[key][subindex][subkey].height
                ).then((res) => (returnData[key][subindex][subkey] = res));
              }
            });
          });
        } else {
          if (returnData[key].isImage) {
            getImage(
              returnData[key].url,
              returnData[key].width,
              returnData[key].height
            ).then((res) => (returnData[key] = res));
          }
        }
      }
    });

    return returnData;
  };

  useEffect(() => {
    setNewData(convertData(data));
  }, [data]);

  const handler = new TemplateHandler();

  const loadFile = async (url) => {
    const response = await fetch(url, {
      method: "GET",
      headers: { "Cache-Control": "no-cache" },
    });
    const template = await response.blob();
    return template;
  };

  const getImage = async (url, width, height) => {
    const response = await fetch(url, {
      method: "GET",
      headers: { "Cache-Control": "no-cache" },
    });

    const blob = await response.blob();
    return {
      _type: "image",
      source: blob,
      format: blob.type,
      width: width,
      height: height,
    };
  };

  function saveFile(filename, blob) {
    const blobUrl = URL.createObjectURL(blob);

    let link = document.createElement("a");
    link.download = filename;
    link.href = blobUrl;

    document.body.appendChild(link);
    link.click();

    setTimeout(() => {
      link.remove();
      window.URL.revokeObjectURL(blobUrl);
      link = null;
    }, 0);
  }

  const generateDocument = async () => {
    const template = await loadFile(filepath);

    const doc = await handler.process(template, newData);

    saveFile(filename, doc);
  };

  return render(generateDocument);
};

WordTemplateReplace.defaultProps = {
  render: (generateDocument) => (<button onClick={() => generateDocument()}>Word-Document</button>)
}

export default WordTemplateReplace;
